import React from 'react'
import { withStyles } from '@material-ui/core/styles'

import Tabs from '@material-ui/core/Tabs'
import Tab from '@material-ui/core/Tab'
import Typography from '@material-ui/core/Typography'

import ListContent from '../ListContent/ListContent'

const styles = () => ({
  root: {},
  description: {
    margin: '.5rem'
  }
})

const TabsContent = (props) => {
  const {classes, selectTab, handleTabChange, tabList} = props

  return (
    <div className={classes.root}>
      <Tabs
        value={selectTab}
        onChange={handleTabChange}
        textColor='primary'
        indicatorColor='primary'>
        {tabList.map((tab, index) => <Tab key={index} label={tab.name} />)}
      </Tabs>
      {
        tabList.map((tab, index) => {
          if (selectTab === index) {
            const ListComponent = tab.options
              ? <ListContent key={`list_${index}`} optionList={tab.options} />
              : null
            return [
              (
                <Typography
                  key={`description_${index}`}
                  component='div'
                  className={classes.description}>
                  {tab.description}
                </Typography>
              ),
              ListComponent
            ]
          }
          return null
        })
      }
    </div>
  )
}

export default withStyles(styles)(TabsContent)



