import * as actionTypes from './actionTypes'

export const setDataLoaded = (isDataLoaded) => {
  return {
    type: actionTypes.SET_DATA_LOADED,
    payload: {
      isDataLoaded
    }
  }
}

export const setCurrentRoute = (currentRoute) => {
  return {
    type: actionTypes.SET_CURRENT_ROUTE,
    payload: {currentRoute}
  }
}

export const toggleModal = () => {
  return {
    type: actionTypes.TOGGLE_MODAL
  }
}

export const updateSize = (size) => {
  return {
    type: actionTypes.UPDATE_SIZE,
    payload: {
      size
    }
  }
}

export const updateSkill = (skillProps) => {
  return {
    type: actionTypes.UPDATE_SKILL,
    payload: {
      skillProps
    }
  }
}

export const setSelectSkill = (selectSkill) => {
  return {
    type: actionTypes.SET_SELECT_SKILL,
    payload: {
      selectSkill
    }
  }
}



