import React, {Component} from 'react'
import { connect } from 'react-redux'
import { withStyles } from '@material-ui/core/styles'
import * as actionCreators from '../../../store/actions'

import Grid from '@material-ui/core/Grid'
import Dialog from '@material-ui/core/Dialog'
import withMobileDialog from '@material-ui/core/withMobileDialog'
import DialogActions from '@material-ui/core/DialogActions'
import DialogContent from '@material-ui/core/DialogContent'
import DialogTitle from '@material-ui/core/DialogTitle'
import Button from '@material-ui/core/Button'

import InfoIcon from '@material-ui/icons/Info'

import lightBlue from '@material-ui/core/colors/lightBlue'

import ListContent from './ListContent/ListContent'
import TabsContent from './TabsContent/TabsContent'

const styles = theme => ({
  root: {
    width: '60%',
    [theme.breakpoints.down('md')]: {
      width: '100%'
    }
  },
  notice: {
    margin: '.5rem'
  },
  info: {
    color: lightBlue[500],
    paddingTop: '.5rem'
  },
  link: {
    textDecoration: 'none'
  }
})

class AppModal extends Component {
  constructor () {
    super()
    this.state = {
      selectTab: 0
    }
  }

  handleTabChange = (event, value) => {
    this.setState({selectTab: value})
  }

  render () {
    const {selectTab} = this.state
    const {
      classes,
      isModalOpen,
      toggleModal,
      fullScreen,
      selectSkill,
      updateSkill
    } = this.props
    const {
      title,
      description = [],
      type,
      options = [],
      tabs = []
    } = selectSkill.skill || {}

    return (
      <Dialog
        open={isModalOpen}
        onClose={toggleModal}
        fullScreen={fullScreen}
        aria-labelledby='form-dialog-title'
        classes={{
          paper: classes.root
        }}>
        <DialogTitle id='form-dialog-title'>
          {title}
        </DialogTitle>
        {
          description.map((item, index) => {
            const contentTitle = item.subTitle
              ? (
                <DialogTitle key={`title_${index}`}>
                  {item.subTitle}
                </DialogTitle>
              )
              : null
            return [
              contentTitle,
              (
                <DialogContent key={`content_${index}`}>
                  <Grid container>
                    <Grid item container xs={12} className={classes.productImageArea}>
                      <p>{item.content}</p>
                      {
                        item.notice &&
                        [
                          (
                            <Grid item xs={1} key={`info_${index}`}>
                              <InfoIcon className={classes.info} />
                            </Grid>
                          ),
                          (
                            <Grid item xs={11} key={`notice_${index}`}>
                              <p className={classes.notice}>
                                {item.notice}
                                {
                                  item.link &&
                                  <a href={item.link} className={classes.link}>{item.link}</a>
                                }
                              </p>
                            </Grid>
                          )
                        ]
                      }
                    </Grid>
                  </Grid>
                </DialogContent>
              )
            ]
          })
        }
        <DialogContent>
          {type === 'LIST' && <ListContent optionList={options} />}
          {
            type === 'TAB' &&
            <TabsContent selectTab={selectTab} tabList={tabs} handleTabChange={this.handleTabChange} />
          }
        </DialogContent>
        <DialogActions>
          <Button color='primary' onClick={() => {
            selectSkill.isFinish = true
            updateSkill(selectSkill)
            toggleModal()
          }}>
            我都會了啊
          </Button>
          <Button color='primary' onClick={() => {
            selectSkill.isFinish = false
            updateSkill(selectSkill)
            toggleModal()
            this.setState({selectTab: 0})
          }}>
            痾，不太熟
          </Button>
        </DialogActions>
      </Dialog>
    )
  }
}

const mapStateToProps = state => {
  return {
    isModalOpen: state.isModalOpen,
    selectSkill: state.selectSkill
  }
}

const mapDispatchToProps = dispatch => {
  return {
    toggleModal: () => dispatch(actionCreators.toggleModal()),
    updateSkill: (skillProps) => dispatch(actionCreators.updateSkill(skillProps))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(withMobileDialog()(AppModal)))



