import * as actionTypes from './actionTypes'

const API_SERVICE = 'https://the-f2e-service.firebaseio.com'

export const getSkillTreeList = () => {
  return (dispatch) => {
    fetch(`${API_SERVICE}/skillTree.json`)
      .then(res => res.json())
      .then(data => {
        dispatch({
          type: actionTypes.GET_SKILL_TREE_LIST,
          payload: {
            skillTreeList: data
          }
        })
      })
  }
}



