import React, { Component } from 'react'
import { connect } from 'react-redux'
import * as actionCreators from '../store/actions'

import SkillTreeContent from '../component/SkillTree/SkillTreeContent'

class SkillTree extends Component {
  componentDidMount () {
    const {setDataLoaded, getSkillTreeList} = this.props

    setDataLoaded(false)
    getSkillTreeList()
  }

  render () {
    return (
      <main>
        <SkillTreeContent {...this.props} />
      </main>
    )
  }
}

const mapStateToProps = state => {
  return {
    layerList: state.layerList,
    isDataLoaded: state.isDataLoaded,
    size: state.size
  }
}

const mapDispatchToProps = dispatch => {
  return {
    getSkillTreeList: () => dispatch(actionCreators.getSkillTreeList()),
    setDataLoaded: (isDataLoaded) => dispatch(actionCreators.setDataLoaded(isDataLoaded)),
    toggleModal: () => dispatch(actionCreators.toggleModal()),
    updateSize: (size) => dispatch(actionCreators.updateSize(size)),
    updateSkill: (skillProps) => dispatch(actionCreators.updateSkill(skillProps)),
    setSelectSkill: (selectSkill) => dispatch(actionCreators.setSelectSkill(selectSkill))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(SkillTree)



