import React, {Component} from 'react'
import { withStyles } from '@material-ui/core/styles'

import Grid from '@material-ui/core/Grid'

import AppModal from '../Common/AppModal/AppModal'
import Block from './Block/Block'
import Curve from './Curve/Curve'

const styles = theme => ({
  root: {
    minHeight: '100vh',
    textAlign: 'center'
  },
  loader: {
    margin: '5rem',
    textAlign: 'center'
  }
})

class AppDemoContent extends Component {
  constructor () {
    super()
    this.state = {}
    this.main = null
  }

  componentDidMount () {
    const {updateSize} = this.props
    updateSize({
      width: this.main.clientWidth,
      height: window.innerHeight
    })
    window.addEventListener('resize', this.onResize)
  }

  componentWillUnmount () {
    window.removeEventListener('resize', this.onResize)
  }

  onResize = (event) => {
    const {updateSize} = this.props
    const {innerHeight} = event.target
    updateSize({
      width: this.main.clientWidth,
      height: innerHeight
    })
  }

  onToggleSkill = (layerIndex, skillIndex, skill) => {
    const {updateSkill, setSelectSkill, toggleModal} = this.props

    if (skill.autoFinish) {
      updateSkill({
        layerIndex,
        skillIndex,
        isFinish: !skill.isFinish
      })
    } else {
      setSelectSkill({
        layerIndex,
        skillIndex,
        skill,
        isFinish: !skill.isFinish
      })
      toggleModal()
    }
  }

  render () {
    const {
      classes,
      isModalOpen,
      modalType,
      toggleModal,
      layerList,
      size
    } = this.props
    const {width} = size

    return (
      <div
        className={classes.root}
        ref={ref => { this.main = ref }}>
        <Grid container>
          {
            layerList.map((layer, layerIndex) => {
              const layerSkillCount = layer.skillList.length
              const curveProps = {
                width,
                layer,
                routeConfig: []
              }
              const blockList = layer.skillList.map((skill, skillIndex) => {
                curveProps.routeConfig.push({
                  isFinish: skill.isFinish,
                  routeTypeList: skill.routeTypeList
                })

                return (
                  <Grid item xs={12 / layerSkillCount} key={`block_${skillIndex}`}>
                    <Block skill={skill} onToggle={() => this.onToggleSkill(layerIndex, skillIndex, skill)} />
                  </Grid>
                )
              })
              const CurveArea = width && layer.needCurve
                ? <Curve key={`bottom_${layerIndex}`} type='bottom' {...curveProps} />
                : null

              return [
                blockList,
                CurveArea
              ]
            })
          }
        </Grid>
        <br />
        <AppModal
          isModalOpen={isModalOpen}
          modalType={modalType}
          toggleModal={toggleModal} />
      </div>
    )
  }
}

export default withStyles(styles)(AppDemoContent)



