import React from 'react'
import { withStyles } from '@material-ui/core/styles'

import yellow from '@material-ui/core/colors/yellow'

const styles = () => ({
  root: {
    display: 'inline-block',
    textAlign: 'center',
    background: `
      linear-gradient(
        60deg,
        rgba(0, 0, 0, 0.5),
        rgba(0, 0, 0, .2),
        rgba(0, 0, 0, .5),
        ${yellow[900]},
        ${yellow['A700']}
      )
    `,
    backgroundSize: '500% 500%',
    boxShadow: '0 0.2rem 0.2rem rgba(0,0,0,.3)',
    transition: 'all .5s',
    color: '#fff',
    margin: '.5rem',
    padding: '1rem',
    cursor: 'pointer'
  },
  finish: {
    color: yellow[50],
    backgroundPositionX: '90%'
  }
})

const Block = (props) => {
  const {classes, style, skill, onToggle} = props
  const {title, isFinish} = skill

  return (
    <span
      style={{...style}}
      className={`${classes.root} ${isFinish ? classes.finish : ''}`}
      onClick={onToggle}>
      {title}
    </span>
  )
}

export default withStyles(styles)(Block)



