import React, {Component} from 'react'
import { withStyles } from '@material-ui/core/styles'
import {
  getCurveRoute
} from '../../../Util/curveUtil'

import yellow from '@material-ui/core/colors/yellow'

const styles = () => ({
  root: {
    opacity: '0.7'
  }
})

class Curve extends Component {
  constructor () {
    super()
    this.setState = {}
    this.curve = null
  }

  componentDidMount () {
    this.props.width && this.drawCurve()
  }

  componentDidUpdate () {
    if (!this.props.width) return
    const curveCtx = this.curve.getContext('2d')
    curveCtx.clearRect(0, 0, this.curve.width, this.curve.height)
    this.drawCurve()
  }

  drawCurve = () => {
    const {width, routeConfig} = this.props
    const curveCtx = this.curve.getContext('2d')

    routeConfig.forEach(config => {
      const {isFinish, routeTypeList} = config

      routeTypeList.forEach(routeType => {
        const curveRoute = getCurveRoute(routeType, width)

        curveCtx.beginPath()
        curveCtx.moveTo(...curveRoute.begin)
        curveCtx.quadraticCurveTo(...curveRoute.bezier)
        curveCtx.lineWidth = 5
        curveCtx.strokeStyle = isFinish ? yellow[900] : 'black'
        curveCtx.stroke()

        if (curveRoute.arrowBegin) {
          curveCtx.beginPath()
          curveCtx.moveTo(...curveRoute.arrowBegin)
          curveCtx.quadraticCurveTo(...curveRoute.arrowBezier)
          curveCtx.lineWidth = 5
          curveCtx.strokeStyle = isFinish ? yellow[900] : 'black'
          curveCtx.stroke()
        }
      })
    })
  }

  render () {
    const {classes, width} = this.props

    return (
      <div>
        {
          width &&
          <canvas
            className={`${classes.root}`}
            width={width}
            height='100'
            ref={ref => { this.curve = ref }} />
        }
      </div>
    )
  }
}

export default withStyles(styles)(Curve)



