import {
  TOP,
  CENTER,
  BOTTOM
} from '../constant'

export const getCurveRoute = (routeType, width) => {
  switch (routeType) {
    case TOP.LEFT:
      return {
        begin: [width * 0.1, 10],
        bezier: [width * 0.2, 80, width * 0.3, 90],
        arrowBegin: [width * 0.28, 70],
        arrowBezier: [width * 0.36, 106, width * 0.25, 95]
      }
    case TOP.RIGHT:
      return {
        begin: [width * 0.9, 10],
        bezier: [width * 0.8, 80, width * 0.7, 90],
        arrowBegin: [width * 0.72, 70],
        arrowBezier: [width * 0.64, 106, width * 0.75, 95]
      }
    case CENTER.LEFT:
      return {
        begin: [width * 0.5, 10],
        bezier: [width * 0.45, 50, width * 0.5, 90],
        arrowBegin: [width * 0.45, 80],
        arrowBezier: [width * 0.53, 120, width * 0.52, 70]
      }
    case CENTER.RIGHT:
      return {
        begin: [width * 0.5, 10],
        bezier: [width * 0.55, 50, width * 0.5, 90],
        arrowBegin: [width * 0.5, 60],
        arrowBezier: [width * 0.46, 120, width * 0.55, 80]
      }
    case BOTTOM.LEFT:
      return {
        begin: [width * 0.3, 10],
        bezier: [width * 0.2, 20, width * 0.1, 90],
        arrowBegin: [width * 0.1, 60],
        arrowBezier: [width * 0.07, 120, width * 0.15, 80]
      }
    case BOTTOM.RIGHT:
      return {
        begin: [width * 0.7, 10],
        bezier: [width * 0.8, 20, width * 0.9, 90],
        arrowBegin: [width * 0.9, 60],
        arrowBezier: [width * 0.93, 120, width * 0.85, 80]
      }
    default:
      return null
  }
}



